@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  caret-color: #268e8f;
  caret-color: var(--s-clr);
}
html::-webkit-scrollbar {
  width: 0px;
}
html {
  --w-clr: #ffffff;
  --w-clr-dark: #f2f2f2;

  --b-clr: #000000;

  --p-clr: #02a4ff;
  --p-clr-light: #59e1ff;
  --s-clr: #268e8f;
  --s-clr-light: #49bdb5;
  --s-clr-dark: #016cb4;
  --g-clr: #a2f263;
  --g-clr-dark: #014a4a;

  --gray-clr-dark: #4d5a62;
  --gray-clr-mid: #b4b4b4;
  --gray-clr-dark-2: #969696;
  --gray-clr-dark: #4d5a62;
}
body {
  color: black;
  background-color: white;
  font-family: "Poppins";
  font-weight: 200 !important;
  font-size: 1.1rem;
}
a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
img {
  object-fit: cover;
}
.row {
  display: block;
  width: 100%;
  position: relative;
}
.container {
  max-width: 1920px;
  display: block;
  margin: auto;
}
.container > * {
  padding: 1rem;
}
input,
select {
  border: none;
  border-bottom: 1px gray solid;
  background: transparent !important;
  padding: 0.5rem;
  font-size: 1.1rem;
  font-weight: 200;
}
input:focus-visible {
  font-weight: 200;
  outline: none;
  border-bottom: 1px #000000 solid;
  border-bottom: 1px var(--b-clr) solid;
}

/* HEADER  */
header {
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 0;
  z-index: 10;
}
.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #00000038;
  border-bottom: 4px var(--s-clr-light) solid;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  top: 0;
  z-index: 2;
  color: white;
  background-image: url(/HyTalent-React/static/media/banner-image.e89c096b.png);
  /* background-size: cover; */
  /* background-attachment: fixed; */
  /* background-position: 50% 0%; */
}
.brand-logo {
  width: 150px;
}
.header-nav-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.header-nav-list-wrapper {
  padding-block: 0.4rem;

  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}
.registeration-nav-list-wrapper {
  color: var(--p-clr-light);
}
.nav-toggle {
  display: none;
}
li {
  list-style-position: inside;
}
p {
  margin-bottom: 0.5rem;
}
/* HERO PAGE */

.hero-wrapper {
  min-height: 600px;
  background-color: orange;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  color: var(--w-clr);
  background-image: url(/HyTalent-React/static/media/banner-image.e89c096b.png);
  background-size: cover;
  background-position: center;
  /* background-attachment: fixed; */
}
.about-item.about-info {
  background: white;
  padding-left: 5rem;
  padding-top: 5rem;
}
.hero-img-wrapper {
  align-self: center;
}
.hero-img {
  width: 300px;
  height: 300px;
  transition: 1s;
}
.hero-img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* ABPUT */

.about-info-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 3rem;
}
.aboutlist li {
  margin-bottom: 1rem;
}
.about-info-item > h2 {
  font-size: 2rem;
  margin-bottom: 0.2rem;
  font-style: italic;
}
.about-info-lead {
  z-index: 1;
  text-align: justify;
  position: relative;
  /* background-color: red; */
}
.about-info-lead > span {
  background-color: var(--w-clr);

  padding-block: 0.8rem;
  -webkit-padding-end: 1rem;
          padding-inline-end: 1rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: inline-block;
  position: relative;
  font-weight: 700;
  font-size: 1.2rem;
  font-style: italic;
  margin-bottom: 1.5rem;
  z-index: 2;
}

.about-info-lead::after {
  content: "";
  /* width: 700px; */
  height: 7px;
  width: 100%;
  left: 0%;
  top: 26%;
  background-color: var(--s-clr);
  position: absolute;

  z-index: 0;
}

/* ABOUT-HOME */
.about-home-wrapper {
  border-top: 10px var(--p-clr-light) solid;
}
.about-card {
  text-align: justify;
  color: white;
  min-width: 399px;
  margin-bottom: 1.5rem;
}
.about-card-name {
  text-align: center;
  font-size: 1.5rem;
  padding: 0.3rem;
  font-weight: 600;
}
.about-card-name-cut {
  margin-bottom: 0.2rem;
}
.about-card-info {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  min-height: 400px;
  max-width: 400px;
  justify-content: center;
}
.about-card-info > * {
  margin-bottom: 1.5rem;
}
.about-card-info-1 {
  padding-block: 2rem;
}
.about-card-2 .about-card-name {
  background-color: var(--s-clr-light);
}
.about-card-2 .about-card-info {
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: var(--s-clr);
}
.about-card-1 .about-card-info {
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: #0277b9;
}
.about-card-1 {
  padding-block: 1rem;
  background-color: #0277b9;
}

.about-home-wrapper {
  display: flex;
  position: relative;
}
.side-bar-TY {
  position: absolute;
  height: 100%;
  width: 10px;
  background-color: var(--s-clr-light);
  z-index: 1;
}
.about-card-wrapper {
  margin-top: -13rem;
  /* flex: 1 0 413px; */
  -webkit-padding-end: 3vmax;
          padding-inline-end: 3vmax;
}
/* //footer */

.footer-wrapper {
  background: var(--s-clr-dark);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer-header {
  font-size: 1.5rem;
  font-style: italic;
}
.footer-flex {
  display: flex;
  padding-top: 2rem;
  grid-gap: 1rem;
  gap: 1rem;
}
.scan {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.scan img {
  background-color: white;
  width: 100px;
  height: 100px;
}
.u-wrapper {
  /* width: 250px; */
  padding: 1rem;
  border-radius: 20px;
  border: 3px white solid;
  margin: 0.3rem;
}
.u-wrapper .u-name {
  text-align: center;
  margin-top: -3rem;
}
.u-name p {
  position: relative;
  top: -100%;
  padding-inline: 0.5rem;
  background: var(--s-clr-dark);
  background-color: red;
  width: 100%;
  margin-top: 0;
}
.u-wrapper .u-name span {
  display: block;
  position: relative;
  top: 3px;
  background: #016cb4;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1rem 0.4rem;
  margin-inline: auto;
}

/* Register PAge */
.register-wrapper-header {
  color: var(--w-clr);
  background-color: var(--s-clr);
  font-size: 1rem;
  padding: 0.6rem 4rem;
}
.register-form-item-header {
  background-color: #49bdb563;
  padding: 0.7rem 2.6rem;
  font-weight: 300;
}
.register-input-wrapper {
  padding: 1rem 2.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  align-items: center;
}
.register-form-wrapper {
  padding: 0 !important;
  border-left: 20px var(--s-clr) solid;
  padding-top: 2rem !important;
  background: var(--w-clr-dark);
}

select {
  padding: 0.5rem;
}
.input-flex {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.input-flex > * {
  flex: 1 1 100%;
}
.input-no-flex {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.input-no-flex > * {
  flex: 0 1 100%;
}
.flex2 {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: flex-start;
}
.grid-form-3 {
  grid-template-columns: 1fr 3fr 0fr;
}
.input-file-wrapper > * {
  flex: 0 0 70%;
}
.input-file {
  /* background-color: red; */
  overflow: hidden;
  position: relative;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}
.input-file label {
  text-decoration: underline;
}
select {
  padding: 0.5rem;
  width: 263px;
}
.input-file input {
  position: absolute;
  width: 100%;
  height: 100%;
  left: -23%;
  top: 10%;
  opacity: 0;
  background: red !important;
}
.input-file:hover * {
  color: var(--s-clr);
  cursor: pointer;
}
.input-file i {
  cursor: pointer;
  transition: 0.5s;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.input-file:hover i {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.register-textbox {
  flex-direction: column !important;
  display: flex;
  align-items: flex-start;
}
.register-textbox > * {
  flex: 1 1 100%;
  width: 100%;
  margin-block: 0.4rem;
}
.register-textbox textarea {
  min-height: 100px;
}
.btn {
  cursor: pointer;
  padding: 0.7rem;
  font-size: 1.1rem;
  text-transform: uppercase;
  color: var(--w-clr);
  background: var(--s-clr-dark);
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.438);
}

.text-center {
  text-align: center;
}
.label-lead {
  color: gray;
  font-size: 0.8em;
}

@media only screen and (max-width: 800px) {
  .scan {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    grid-gap: 1rem;
    gap: 1rem;
    padding-block: 1rem;
    flex: 1 1 100%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .about-item.about-info {
    padding: 0;
  }
  .about-card-wrapper {
    margin-top: 0;
  }
  .about-home-wrapper {
    display: flex;
    position: relative;
    flex-wrap: wrap;
  }
  .footer-flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .side-bar-TY {
    display: none;
  }
  .header-wrapper.container {
    flex-direction: column;
    padding: 0.6rem;
    padding-top: 1rem;
    align-items: center;
  }
  ul.header-nav-list-wrapper.component-nav-list-wrapper {
    flex-wrap: wrap;
  }
  .about-card {
    min-width: 280px;
  }
  .header-wrapper.container > * {
    padding: 0;
  }
  .about-info-item {
    padding: 1rem;
  }
  .hero-wrapper {
    min-height: 100vh;
  }
}

